@import url(https://fonts.googleapis.com/css2?family=Lobster&display=swap);
*{margin:0;padding:0;box-sizing:border-box}body{font-family:"Montserrat", sans-serif;font-size:18px}

.navbar{background-color:black;color:white;display:flex;justify-content:space-between;padding:1em 6em;position:relative}.navbar .logo{font-family:"lobster", sans-serif;font-weight:200}.navbar .logo a{color:white;text-decoration:none}.navbar nav{display:flex;align-items:center}.navbar nav li{display:inline-block;margin-left:2em;list-style:none;color:white;text-decoration:none}.navbar nav li:hover{text-decoration:underline;cursor:pointer}.navbar .burger{display:none;flex-direction:column;justify-content:center}.navbar .burger-line{display:block;width:2em;height:4px;background-color:white;border-radius:2px;transition:transform 0.25s ease}.navbar .burger-line:first-child{margin-bottom:5px}.navbar .burger-line:nth-child(3){margin-top:5px}@media screen and (max-width: 1075px){.navbar{z-index:999;position:fixed;top:0;left:0;width:100%;padding:1em 4em}nav{visibility:hidden;height:0px;opacity:0;margin-top:4em;position:absolute;background-color:black;width:100%;justify-content:flex-end;left:0;top:0;padding:1em 6em;text-align:end;transition:visibility 0.25s ease, height 0.4s ease, opacity 0.165s ease 0.1s}nav ul{display:flex;flex-direction:column;visibility:hidden}nav ul a{margin-bottom:1em}.burger{display:flex !important}}.show-nav{visibility:visible;height:10rem;opacity:1;transition:visibility 0.1s ease, height 0.25s ease, opacity 0.25s ease}.show-links{visibility:visible}.first-line{transform:rotate(45deg) translateY(10px)}.second-line{transform:rotate(-45deg) translateY(-5px) translateX(-3px)}.third-line{transform:rotate(-45deg) translateY(-11px) translateX(3px)}

.Home__container{padding:6em;position:relative;display:flex;justify-content:center}.Home__container img{max-width:100%;min-width:10rem;height:auto}.Home__container .shop-men{margin-right:1em}

.ProdFilter{padding:1em;margin-right:2em;border:2px solid #e6e6e6;height:100%;width:20rem}.ProdFilter h2{margin-bottom:1em;text-align:center}.ProdFilter .filter{padding:0.5em 0}.ProdFilter .filter hr{margin:1em 0;border-top:1px solid #b6b6b6}.ProdFilter .filter-label{display:flex;justify-content:space-between;margin-bottom:0.5em;padding:0 0.2em}.ProdFilter .filter-label .refine{display:flex}.ProdFilter .filter-label .refine span{margin-right:0.25em}.ProdFilter .filter-label .refine2{display:none}.ProdFilter .filter-label a{text-decoration:none;color:#818181;cursor:pointer}.ProdFilter .filter-items{background-color:#eeeeee;padding:0.5em}.ProdFilter .filter-items h4{margin-bottom:0.5em}.ProdFilter .filter-items .checkboxes{margin-bottom:0.5em}.ProdFilter .filter-items .checkboxes input{margin-right:0.5em}.ProdFilter .filter .btn-close{display:none}.ProdFilter .filter-label-mobile{display:none}@media screen and (max-width: 1075px){.ProdFilter{all:unset;display:flex;width:100%;justify-content:space-between;align-items:center;position:relative;margin-bottom:4em}.ProdFilter h2{margin:0}.ProdFilter hr{display:block;position:absolute;margin-top:6em;width:100%;border-top:1px solid #b6b6b6}.ProdFilter .filter{display:flex;position:fixed;flex-direction:column;background-color:white;z-index:998;overflow:scroll;bottom:0;left:0;padding:6em 2em 1em 2em;width:100%;height:0;visibility:hidden;opacity:0;transition:visibility 0.1s ease, height 0.25s ease, opacity 0.25s ease}.ProdFilter .filter-label .refine{display:none}.ProdFilter .filter-label .refine2{display:flex}.ProdFilter .filter .btn-close{display:flex;width:100%;justify-content:flex-end}.ProdFilter .filter .btn-close .close-line{display:block;width:1.8em;height:4px;background-color:black;border-radius:2px;transition:transform 0.25s ease;margin-bottom:1em}.ProdFilter .filter .btn-close .close-line:first-child{margin-bottom:5px;transform:rotate(45deg)}.ProdFilter .filter .btn-close .close-line:nth-child(2){transform:rotate(-45deg) translateY(-6px) translateX(6px)}.ProdFilter .filter hr{display:none}.ProdFilter .filter-label-mobile{display:flex !important;position:absolute;right:0}.ProdFilter .filter-label-mobile .refine span{margin-right:0.25em}}.show{height:100% !important;visibility:visible !important;opacity:1 !important}@media screen and (max-width: 600px){.ProdFilter{display:flex;flex-direction:column}.ProdFilter .filter-label-mobile{position:relative;margin-top:1em}}

.ProdList{width:100%;display:grid;grid-template-columns:repeat(auto-fit, minmax(18rem, 1fr));justify-items:center}.ProdList .No-results{display:none}.ProdList a{text-decoration:none;width:18rem;height:35rem;color:black;margin-bottom:4em}.ProdList .Card{padding:0.2em;border:2px solid transparent;width:17.7rem;height:35rem;position:relative}.ProdList .Card:hover{border:2px solid #e6e6e6;cursor:pointer}.ProdList .Card *{margin-bottom:0.5em}.ProdList .Card__img{max-height:25rem;width:17rem;border:1px solid #e6e6e6}.ProdList .Card__price{position:absolute;right:0.2em;bottom:0}.show{display:flex}

.Bag__container{padding:6em;position:relative}.Bag__container .ItemsList hr{margin-bottom:2rem;border-top:1px solid #c7c7c7}.Bag__container .ItemsList__heading{margin-bottom:2rem}.Bag__container .ItemsList__item{display:flex;margin-bottom:2rem}.Bag__container .ItemsList__item__img img{height:12rem;margin-right:2rem}.Bag__container .ItemsList__item__rem{flex-grow:2;display:flex;justify-content:flex-end}.Bag__container .ItemsList__item__rem p{margin-left:4rem;margin-right:4rem}.Bag__container .ItemsList__item__rem a:hover{cursor:pointer;text-decoration:underline}.Bag__container .ItemsList__checkout{display:flex;justify-content:flex-end;align-items:center}.Bag__container .ItemsList__checkout a{display:block;text-decoration:none;color:white;padding:1em;margin-left:1em;background-color:black;text-align:center;cursor:pointer}.Bag__container .ItemsList .bag-empty{border:2px solid #e6e6e6;background-color:#eeeeee;padding:5em;display:flex;flex-direction:column;align-items:center;justify-content:center}.Bag__container .ItemsList .bag-empty i{font-size:10rem;color:#c7c7c7;margin-bottom:1rem}.Bag__container .ItemsList .bag-empty h1{margin-bottom:1rem}.Bag__container .ItemsList .bag-empty p{text-align:center}.Bag__modal{display:none}.show{display:flex}.hide{display:none}@media screen and (max-width: 650px){.Bag__container{padding:6em 4em}.Bag__container .ItemsList__item{display:flex;flex-direction:column}.Bag__container .ItemsList__item{flex-direction:column}.Bag__container .ItemsList__item__img{margin-bottom:2em;text-align:center}.Bag__container .ItemsList__item__desc{margin-bottom:2em}.Bag__container .ItemsList__item__rem{justify-content:flex-start}}

.CheckoutModal{position:fixed;width:100%;height:100vh;top:0;left:0;background-color:rgba(0,0,0,0.5);display:flex;justify-content:center;align-items:center}.CheckoutModal__modal{background-color:white;width:50%;height:50%}.CheckoutModal__modal .container{padding:2em;height:100%;width:auto;display:flex;flex-direction:column;justify-content:center;align-items:center}.CheckoutModal__modal .container h1{margin-bottom:1em;text-align:center}.CheckoutModal__modal .container h2{margin-bottom:1.5em}.CheckoutModal__modal .container .content{display:flex;flex-direction:column;justify-content:center;align-items:center}.CheckoutModal__modal .container .content .actions{display:flex;justify-content:center;flex-wrap:wrap}.CheckoutModal__modal .container .content .actions a{display:block;min-width:10rem;text-decoration:none;color:white;padding:1em;margin:0 1em 1em 1em;background-color:black;text-align:center;cursor:pointer}.CheckoutModal__modal .container .thank-you{display:none;text-align:center;flex-direction:column}.CheckoutModal__modal .container .thank-you span i{font-size:6rem;margin-bottom:0.2em}.show{display:flex !important}.hide{display:none !important}@media screen and (max-width: 650px){.CheckoutModal__modal{width:75%;height:65%}}

.Men__container{padding:6em;position:relative;display:flex;justify-content:center}@media screen and (max-width: 1075px){.Men__container{padding:6em 4em;display:flex;flex-direction:column;align-items:center}}@media screen and (max-width: 600px){.Men__container{padding:6em 1em;display:flex;flex-direction:column;align-items:center}}

.Product__container{padding:6em;position:relative;display:flex;justify-content:center;flex-wrap:wrap}.Product__container .img-list{display:flex;flex-direction:column;margin-right:1em;margin-bottom:2em}.Product__container .img-list img{height:5.5rem;width:4rem;margin-bottom:1em;border:1px solid #8b8b8b}.Product__container .img-list img:hover{cursor:pointer}.Product__container .main-img{margin-right:2em;margin-bottom:4em}.Product__container .main-img img{width:20rem;height:30rem}.Product__container .desc{margin-right:5em;margin-bottom:4em;width:20rem}.Product__container .desc__brand{margin-bottom:0.2em}.Product__container .desc__name{margin-bottom:1em}.Product__container .desc .incentives{margin-top:3em}.Product__container .desc .incentives__content{display:flex;justify-content:space-between;margin-bottom:1em}.Product__container .desc .incentives__content span{background-color:black;color:white;width:4em;text-align:center}.Product__container .purchase-option{margin-bottom:4em}.Product__container .purchase-option .authenticity{display:flex;margin-bottom:2em}.Product__container .purchase-option .authenticity i{margin-right:0.5em}.Product__container .purchase-option h4{margin-bottom:0.5em}.Product__container .purchase-option a{display:block;margin-top:2em;text-decoration:none;color:white;padding:1em;background-color:black;text-align:center;cursor:pointer}@media screen and (max-width: 700px){.Product__container{padding:6em 1em}.Product__container .main-img{order:-1;margin-right:0px}.Product__container .img-list{flex-direction:row;margin-right:0px;flex-wrap:wrap;justify-content:center}.Product__container .img-list img{margin:0 1em 1em 1em}}

.Women__container{padding:6em;position:relative;display:flex;justify-content:center}@media screen and (max-width: 1075px){.Women__container{padding:6em 4em;display:flex;flex-direction:column;align-items:center}}@media screen and (max-width: 600px){.Women__container{padding:6em 1em;display:flex;flex-direction:column;align-items:center}}

